.onboardingContainer {
    min-height: 100vh;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1000;
    left: 0;
    background-color: #fff;
}

.onboardingBackground {
    background-image: url('../../public/images/background-onboarding.jpg');
    padding-top: 140px;
    min-height: 100vh;
    background-repeat: no-repeat;
    background-size: 100%;
}

.onboardingPanel {
    width: 540px;
    height: 390px;
    background-color: #334254;
    box-shadow: inset 0 -1px 0 0 #202a35;
    margin: 0 auto;
    padding-top: 51px;
}

.logo {
  margin: 0 auto;
  display: block;
  width: 123px;
  height: 107px;
}

.sliderTitle {
  font-size: 45px;
  font-weight: 100;
  line-height: 0.71;
  letter-spacing: 6.9px;
  color: #dddddd;
  text-align: center;
  margin: 42px 0 26px;
}

.sliderText {
  width: 405px;
  height: 56px;
  font-size: 24px;
  font-weight: 300;
  letter-spacing: 0.8px;
  text-align: center;
  color: #ffffff;
  margin: 0 auto;
}

.btnOnboarding {
  width: 150px;
  height: 40px;
  background-color: #c8c951;
  border: solid 1px #4a4a4a;
  margin: 26px auto;
  display: block;
  text-transform: uppercase;
  cursor: pointer;
  font-size: 14px;
  letter-spacing: 1px;
  color: #4a4a4a;
}

ol {
  padding: 0;
  text-align: center;
}

.dotIndicator {
    display: inline-block;
    width: 6px;
    height: 6px;
    margin: 1px 0 1px 2px;
    text-indent: -999px;
    cursor: pointer;
    background-color: #000\9;
    background-color: rgba(0,0,0,0);
    border: 1px solid #fff;
    border-radius: 10px;
}

.dotIndicator.active {
    width: 8px;
    height: 8px;
    margin: 0;
    background-color: #fff;
}

.closeOnboarding {
  color: #fff;
  float: right;
  font-size: 22px;
  margin: -31px 22px;
  cursor: pointer;
}

@media screen and (max-width: 540px) {
  .onboardingContainer .onboardingBackground{
    padding-top: 0;
  }
  .onboardingPanel,
  .sliderText {
    width: 100%;
  }
  .onboardingPanel {
    min-height: 100vh;
  }
}


