@import '../variables';

.header {
  position: fixed;
  top: 6px;
  height: $headerHeight;
  width: 50%;
  left: 25%;
  z-index: 200;

  .header-container {
    @include display-flex;
    @include flex-direction(row);
    @include align-items(center);

    .header-logo {
      width: $navigationWidth;
      text-align: center;
      vertical-align: bottom;

      .header-icon, img {
        width: 32px;
        height: 28px;
        display: inline-block;
        vertical-align: top;
        margin-right: 10px;
      }

      font-family: $fontFamily;
      font-size: 20px;
      font-weight: 100;
      letter-spacing: 2.6px;
      color: #dddddd;
      line-height: 28px;

    }

    .header-search {
      @include flex(1);
      display: none;

      .input {
        @include display-flex;
        @include flex-direction(row);
        @include align-items(center);

        padding-right: 16px;
        height: 40px;
        font-family: $fontFamily;
        font-size: 16px;
        letter-spacing: 1px;
        color: #dddddd;
        border: 1px solid #ddd;
        &.input-active {
          background-color: #FFF;
          color: #333;
          i {
            color: #333;
          }
          input {
            color: #333;
            &::-webkit-input-placeholder, &::-moz-placeholder, &:-ms-input-placeholder, &::placeholder {
              color: #333;
            }
          }
        }

        i {
          padding: 10px;
          font-size: 16px;
          color: #dddddd;

          &.delete-icon {
            font-size: 30px;
            cursor: pointer;
          }
        }
        input {
          @include flex(1);
          @include appearance(none);
          border: none;
          background: transparent;
          color: #dddddd;
          font-size: 16px;
          height: 20px;

          &::-webkit-input-placeholder, &::-moz-placeholder, &:-ms-input-placeholder, &::placeholder {
            color: #DDD;
          }
        }
      }
    }

    .header-options {
      padding: 0px 16px;
      ul {
        @include display-flex;
        @include flex-direction(row);
        padding: 0px;
        list-style: none;
        > li {
          list-style: none;
          a {
            font-family: $fontFamily;
            font-size: 14px;
            font-weight: normal;
            color: #dddddd;
            text-decoration: none;
            padding: 10px 16px;
            vertical-align: middle;

            i {
              font-size: 16px;
              margin-right: 6px;
              vertical-align: middle;
            }
          }

          &:hover, &.active {
            a {
              background-color: rgba(255, 255, 255, 0.1);
            }
          }
        }
      }
    }

    @media screen and (max-width: 767px) {
      @include justify-content(center);
      height: 100%;
      .header-search, .header-options {
        display: none;
      }
    }
  }
}



