@import "../variables";

.footer {
  padding-left: $navigationWidth;
  height: $footerHeight;
  line-height: $footerHeight;

  .footer-container {
    padding: 0px 16px;

    font-family: $fontFamily;
    font-size: 14px;
    font-weight: 300;
    letter-spacing: 0.9px;
    color: #4a4a4a;
    text-align: right;
  }

  @media screen and (max-width: 767px) {
    padding-left: 0px;
    .footer-container {
      text-align: center;
    }
  }
}


