@import "../variables";

.documents {
    .filter-select {
        position: relative;
        display: inline-block;
        cursor: pointer;

        select {
            @include appearance(none);
            border: none;
            padding: 0 16px;
            background: none;
            outline: none;

            &:hover {
                text-decoration: underline;
            }
        }

        &:after {
            content: "\f3d0";
            font-family: "Ionicons";
            font-size: 16px;
            display: inline-block;
            margin-left: 2px;
            position: absolute;
            right: 0;
            top: -1px;
            color: #333;
            z-index: -1;
        }
    }

    .path-link a {
        text-decoration: none;
        color: inherit;

        &:hover {
            color: black;
        }
    }

    .dropzone {
        @include flex;
        border-color: transparent;
        margin-top: 5px;
        padding: 2px 2px 50px;

        &.active {
            border-color: steelblue;
            background-color: rgba(10,158,200,0.1);
        }
    }

    .rename-box {
        input {
            height: 40px;
            line-height: 40px;
            vertical-align: bottom;
            display: inline-block;

            font-family: monospace;
            font-size: 24px;
        }

        button.button {
            margin-left: 10px;
            padding: 0 13px;
            height: 40px;

            i {
                margin: 0;
                font-size: 33px;
            }
        }
    }

    .button-import {
        input {
            opacity: 0;
            width: 100%;
            height: 100%;
            position: absolute;
            left: 0;
            right: 0;
            bottom: 0;
            top: 0;
            z-index: -1;
            pointer-events: none;
        }
    }



    .document-list {
        @include display-flex;

        &.view-mode-grid {
            @include flex-direction(row);
            @include justify-content(flex-start);
            @include flex-wrap(wrap);
            margin: 0 -16px;

            .document {
                max-width: 140px;
                margin: 16px;
                position: relative;

                .document-date {
                    display: none;
                }

                .document-icon-container {
                    width: 50vw;
                    height: 50vw;
                    max-width: 140px;
                    max-height: 140px;
                    background-color: #f6f6f6;
                    border: solid 1px #cccccc;
                    overflow: hidden;
                    cursor: pointer;
                    margin-bottom: 16px;
                    @include display-flex;
                    @include align-items(center);
                    @include justify-content(center);

                    .document-icon-content {
                        width: 70%;

                        input[type="checkbox"] {
                            display: none;
                            position: absolute;
                            top: -10px;
                            left: -15px;
                        }

                        .document-icon {
                            display: block;
                            width: 100%;
                            margin: 0 auto;
                        }
                    }
                }

                .document-name {
                    font-family: $fontFamily;
                    font-size: 14px;
                    font-weight: 500;
                    letter-spacing: 1.2px;
                    text-align: center;
                    color: #32363f;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                }

                input[type="checkbox"] {
                    display: none;
                    position: absolute;
                    top: 10%;
                    left: 10%;
                    z-index: 10;
                }

                &.selected,
                &:hover {
                    input[type="checkbox"] {
                        display: block;
                    }

                    .document-icon-container {
                        //background-color: lighten(#f6f6f6, 5%);
                        border: solid 1px #333;

                        .document-icon-content {}
                    }
                }

                &.selected {
                    input[type="checkbox"] {
                        display: block;
                    }

                    .document-icon-container {
                        background-color: #334254;
                        color: #FFF;
                    }

                    .document-name {
                        background-color: #334254;
                        color: #FFF;
                    }

                    .document-name {
                        color: #FFF;
                    }
                }
            }
            @media screen and (max-width: 767px) {
                 @include justify-content(center);

                .document {
                    max-width: 40vw;

                    .document-icon-container {
                        max-width: 40vw;
                        max-height: 40vw;
                    }
                }
            }
        }

        &.view-mode-line {
            @include flex-direction(column);

            .document {
                cursor: pointer;
                margin: 16px 0 0;
                padding: 16px;
                background: #F6F6F6;
                border: 1px solid #CCCCCC;
                @include display-flex;
                @include align-items(center);

                &.selected {
                    background-color: #334254;

                    .document-icon-container {
                        color: #FFF;
                    }

                    .document-name {
                        color: #FFF;
                    }

                    .document-date {
                        color: #FFF;
                    }
                }

                .document-name {
                    @include flex(1);
                }

                .document-date {
                    font-size: 14px;
                    color: #32363F;
                    letter-spacing: 1.17px;
                    font-weight: 100;
                }

                .document-icon-container {
                    width: 60px;
                    height: 51px;
                    margin: 0 16px;

                    .document-icon {
                        width: 100%;
                        height: auto;
                        display: block;
                    }
                }
            }
        }
    }
}



