@import '../variables';

.tabBar {
  height: 48px;
  position: fixed;
  z-index: 998;
  bottom: 0px;
  left: 0px;
  right: 0px;
  background-color: $default;
  border-top: 1px solid #DDD;

  z-index: 0;

  @media screen and (min-width: 768px) {
    display: none;
  }

  ul {
    padding: 0px;
    margin: 0px;
    list-style: none;
    height: 100%;
    @include display-flex;
    @include align-items(center);
    @include justify-content(center);

    li {
      display: inline-block;
      vertical-align: top;
      height: 100%;
      @include display-flex;
      @include align-items(center);
      @include justify-content(center);
      @include flex-direction(column);
      color: #FFF;
      padding: 10px 16px;
      a {
        @include display-flex();
        @include flex-direction(column);
        @include align-items(center);
        @include justify-content(center);
        color: $positive;
        text-decoration: none;
        font-weight: 300;
        color: #FFF;
        font-size: 12px;

        i {
          font-size: 16px;
        }
      }

      &.active, &:hover {
        a {
          color: $positive;
          i {
            color: $positive;
          }
        }
      }
    }
  }
}



