@import "../variables";

.filter-select {
  position: relative;
  display: inline-block;
  cursor: pointer;

  select {
    @include appearance(none);
    border: none;
    padding: 0px 16px;
    background: none;
    outline: none;

    &:hover {
      text-decoration: underline;
    }
  }

  &:after {
    content: "\f3d0";
    font-family: "Ionicons";
    font-size: 16px;
    display: inline-block;
    margin-left: 2px;
    position: absolute;
    right: 0px;
    top: -1px;
    color: #333;
    z-index: -1;
  }
}

.dataList {
  .loading {
    height: 200px;
    max-height: 100vh;
    @include display-flex;
    @include align-items(center);
    @include justify-content(center);
    img {
      height: 80px;
    }
  }

  .data-list {
    @include flex-direction(row);
    @include justify-content(flex-start);
    @include display-flex;
    @include flex-wrap(wrap);
    margin: 0px -16px;

    .data {
      max-width: 144px;
      margin: 16px;

      .data-icon-container {
        width: 50vw;
        height: 50vw;
        max-width: 144px;
        max-height: 144px;
        background-color: #f6f6f6;
        border: solid 1px #cccccc;

        cursor: pointer;
        margin-bottom: 16px;
        @include display-flex;
        @include align-items(center);
        @include justify-content(center);
        .data-icon {
          width: 60%;
          height: 80%;
          border: 1px solid #4a4a4a;
          @include display-flex;
          @include align-items(center);
          @include justify-content(center);

          img {
            display: block;
            max-width: 90%;
          }
        }
      }

      .data-name {
        font-family: $fontFamily;
        font-size: 14px;
        font-weight: 500;
        letter-spacing: 1.2px;
        text-align: center;
        color: #32363f;
      }
      &:hover {
        .data-icon-container {
          background-color: lighten(#f6f6f6, 5%);
          border: solid 1px #333;
        }
      }
    }

    @media screen and (max-width: 767px) {
      @include justify-content(center);

      .data {
        max-width: 40vw;

        .data-icon-container {
          max-width: 40vw;
          max-height: 40vw;
        }
      }
    }
  }
}



