@import "../../variables";

.directory-picker {
    border: 1px solid black;
    overflow: auto;

    .directory-picker-content {
        margin: 10px;
        background-color: white;

        height: 338px;
        text-align: left;
        position: relative;

        ul {
            margin: 0;
        }

        .content {
            list-style: none;
            margin-left: 16px;
            padding-left: 0px;
            // border-left: 1px dotted black;

            position: relative;
        }

        .content li {
            padding: 0;
            position: relative;

            &:hover {
                background-color: inherit;
            }
        }

        .name {
            cursor: pointer;
            font-size: 14px;
            color: #4A4A4A;
            letter-spacing: 1px;
            font-family: "Roboto";
            line-height: 2em;
            display: block;

            -webkit-touch-callout: none; /* iOS Safari */
            -webkit-user-select: none; /* Safari */
             -khtml-user-select: none; /* Konqueror HTML */
               -moz-user-select: none; /* Firefox */
                -ms-user-select: none; /* Internet Explorer/Edge */
                    user-select: none; /* Non-prefixed version, currently
                                          supported by Chrome and Opera */

            i[class=''] {
                padding-right: 15px;
            }

            &:hover, &.selected {
                background-color: #E7E7E7;
            }

            i.ion-ios-arrow-right {
                padding-right: 9px;
            }

            i.ion-ios-arrow-down {
                padding-right: 5px;
            }

            i.ion-ios-folder-outline {
                padding-right: 5px;
            }
        }

    }
}



