@import "../variables";

.contacts {
  .contact {
    @include display-flex;
    @include flex-direction(row);
    @include align-items(center);
    @include justify-content(space-between);
    margin: 16px 0px;
    background-color: #f6f6f6;
    border: solid 1px #cccccc;
    padding: 20px;
    font-weight: 300;
    cursor: pointer;

    .contact-checkbox {
      i {
        font-size: 25px;
      }

      .ion-android-checkbox-outline-blank {
        display: block;
      }

      .ion-android-checkbox-outline {
        display: none;
      }
      &.checked {
        .ion-android-checkbox-outline-blank {
          display: none;
        }

        .ion-android-checkbox-outline {
          display: block;
        }
      }
    }

    .contact-icon {
      width: 30px;
      font-size: 40px;
      margin: 0px 35px;
    }

    .contact-name {
      @include flex(1);
      text-align: left;
      font-weight: normal;
    }

    .contact-telephone {
      @include flex(1);
      text-align: center;
    }

    .contact-email {
      @include flex(1);
      text-align: right;
      margin-right: 30px;
    }

    &:hover, &.active {
      border: solid 1px #4a4a4a;
      .contact-checkbox {
        opacity: 1;
      }
    }
  }

  @media screen and (max-width: 767px) {
    .contact {
      margin: 0px -16px;
      border-left: none;
      border-right: none;

      &:not(:nth-child(2)) {
        border-top: none;
      }
      .contact-checkbox {
        display: none;
      }

      .contact-telephone {
        display: none;
      }

      .contact-email {
        display: none;
      }

      .contact-icon {
        display: none;
      }
    }
  }

  .edit-modal {
    margin: -16px -16px;
    border: 1px solid #DDD;
    .edit-modal-header {
      background-color: #F6F6F6;
      height: 88px;
      @include display-flex;
      @include align-items(center);
      @include justify-content(space-between);
      border-bottom: 1px solid #DDD;
      padding: 16px;
      h1 {
        @include flex(1);        
        font-size: 24px;
        color: #32363F;
        letter-spacing: 1px;
        line-height: 40px;
        font-weight: 500;
      }

      .button {
        margin: 0px 16px;
      }
    }

    .edit-modal-content {
      padding: 16px;
      h3 {
        
        font-size: 16px;
        color: #95999D;
        letter-spacing: 1.14px;
        line-height: 24px;
        margin: 0px;
        margin-bottom: 10px;
        font-weight: lighter;
      }

      p {
        
        font-size: 16px;
        color: #4A4A4A;
        letter-spacing: 1.14px;
        line-height: 24px;
        margin: 0px;
        margin-bottom: 32px;
        font-weight: lighter;
      }

      p + p {
        margin-top: -32px;
      }

      a, a:visited{
        color: #4A4A4A;
      }
    }

    &.edit-modal-form {
      .form-group, .form-group-inline {
        > label {
          /* Prénom: */
          
          font-size: 16px;
          color: #95999D;
          letter-spacing: 1.14px;
          line-height: 24px;
          margin-bottom: 16px;
          display: block;
        }

        > input, select {
          @include appearance(none);
          background: #FFFFFF;
          
          font-size: 16px;
          color: #4A4A4A;
          letter-spacing: 1.14px;
        }

        .form-control {
          border-radius: 2px;
        }


        select:after {
          content: $ionicon-var-ios-arrow-down;
          font-family: $ionicons-font-family;
          display: block;
          position: absolute;
          font-size: 25px;
          right: 16px;
        }
        .form-group-inline {
          @include display-flex;
          @include flex-direction(row);
          @include align-items(center);
          @include justify-content(space-between);
          > input, select {
            margin-right: 16px;
            &:last-child {
              margin-right: 0px;
            }
          }
        }
      }
    }

    @media screen and (max-width: 767px) {
      .edit-modal-header {
        .more-button,
        .update-button,
        .save-button {
          display: none;
        }
      }
    }
  }
}



