@import "../../variables";

.toolBox {
  position: relative;
  z-index: 1;

  &.open {
    .toolBox-content {
      display: block;
    }
  }

  .full-transparent {
      display: block;
      position: fixed;
      background-color: transparent;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
  }

  .toolBox-content {
    position: absolute;
    min-height: 100px;
    min-width: 250px;
    border: 1px solid #9B9B9B;
    background: #F6F6F6;
    z-index: 999;
    right: 0px;
    padding: 6px 0px;
    margin-top: 10px;
    display: none;

    .toolBox-content-triangle {
      position: absolute;
      width: 10px;
      height: 10px;
      border: 1px solid #9B9B9B;
      border-left: none;
      border-bottom: none;
      background-color: #F6F6F6;
      @include transform(rotate(-45deg));
      top: -6px;
      right: 20px;

    }

    ul {
      list-style: none;
      padding: 0px;
      margin: 0px;
      li {
        padding: 16px;
        cursor: pointer;
        
        font-size: 14px;
        color: #4A4A4A;
        letter-spacing: 1px;
        &:hover, &.active {
          background-color: #E7E7E7;
        }
      }
    }
  }
}



