@import "../variables";

.section {
    @include display-flex;
    @include flex-direction(column);
    @include flex;

  .section-header {
    @include display-flex;
    @include flex-direction(row);
    @include align-items(center);
    margin-left: -15px;
    margin-right: -15px;
    border-bottom: 1px solid #DDD;
    padding: 10px 15px;
    h1 {
      font-family: $fontFamily;
      font-size: 12px;
      letter-spacing: 1px;
      color: #32363f;
      margin: 0px;
      font-weight: normal;
      margin-top: 10px;
      @include flex(1);
    }
  }

  .section-content {
      @include display-flex;
      @include flex-direction(column);
      @include justify-content(flex-start);
  }
}



