@import "../variables";

.page {
    @include display-flex;
    @include flex-direction(column);
    @include flex;
    width: 100%;

  .page-header {
    @include display-flex;
    @include flex-direction(row);
    @include align-items(center);
    width: 100%;

    @media screen and (max-width: 767px) {
      @include flex-direction(column);
    }

    .page-back-button {
      width: 48px;
      height: 40px;
      text-align: center;
      background-color: #f6f6f6;
      border: solid 1px #9b9b9b;
      @include display-flex;
      @include align-items(center);
      @include justify-content(center);
      margin-right: 16px;
      cursor: pointer;
      &:hover {
        background-color: lighten(#f6f6f6, 5%);
        i {
          color: darken(#4a4a4a, 5%);
        }
      }
      i {
        color: #4a4a4a;
        font-size: 30px;
      }
    }
    h1 {
      font-family: $fontFamily;
      font-size: 24px;
    //   letter-spacing: 1px;
      color: #32363f;
      margin: 0px;
      font-weight: normal;
      margin-right: 16px;
      @include flex;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    .page-options {
      ul.page-actions {
        list-style: none;
        padding: 0px;
        @include display-flex;
        @include flex-direction(row);
        @include align-items(center);
        > li {
          padding: 0px;
          padding-left: 10px;
          .button {
            @include display-flex;
            @include flex-direction(row);
            @include align-items(center);
            @include justify-content(center);
            @include appearance(none);

            text-align: center;
            border: solid 1px #9b9b9b;
            font-family: $fontFamily;
            font-size: 14px;
            letter-spacing: 1px;
            padding: 10px 16px;
            text-decoration: none;
            font-weight: 300;
            height: 45px;
            box-sizing: border-box;

            i {
              margin-right: 12px;
              font-size: 20px;
            }

            &.button-icon {
              i {
                margin-right: 0px;
              }
            }
            &.button-stable {
              background-color: $stable;
              color: #4a4a4a;

              &:hover {
                background-color: lighten($stable, 5%);
              }

            }

            &.button-default {
              background-color: $default;
              color: #dddddd;

              &:hover {
                background-color: lighten($default, 5%);
              }
            }

            &.button-clear {

            }
          }
        }
      }
    }
  }

  .page-content {

      @include display-flex;
      @include flex;

    h2 {
      font-family: $fontFamily;
      font-size: 30px;
      letter-spacing: 1.3px;
      color: #32363f;
      font-weight: normal;
    }

    p {
      font-family: $fontFamily;
      font-size: 14px;
      letter-spacing: 1px;
      color: #32363f;
      line-height: 20px;
      font-weight: 300;
    }
  }
}



