@import "../../variables";

.importer {

    .hide {
        display: none;
    }

    .importer-header {
        @include display-flex;
        @include justify-content(space-between);
        @include align-items(center);

    }

    .importer-content {
        padding: 25px 0;

        .dropzone {
            height: 355px;
            width: 100%;
            overflow: auto;
            border-color: grey;
            border-width: 2px;
            border-style: dashed;
            border-radius: 5px;
            box-sizing: border-box;
            padding: 5px;


            @include display-flex;
            @include flex-wrap(wrap);

            .dropzone-msg {
                text-align: center;
                text-transform: uppercase;
                color: rgba(0,0,0,0.3);
                font-weight: bold;
                font-size: 50px;
                position: absolute;
                top: 40%;
                left: 50%;
                transform: translate(-50%, -50%);
                padding: 0 50px;
            }
        }

        .import-doc {
            @include display-flex;
            @include align-items(center);
            @include justify-content(space-between);
            @include flex-direction(column);

            border: 1px solid grey;
            border-radius: 10px;
            height: 32vw;
            width: 32vw;
            max-width: 150px;
            max-height: 190px;
            margin: 10px;

            padding: 5px 0;

            .image-box {
                @include display-flex;
                @include justify-content(center);
                @include align-items(center);
                @include flex;
                width: 100%;

                img {
                    border-radius: 5px;
                    max-width: 85%;
                    max-height: 100%;
                    width: 100%;
                }
            }

            span {
                margin: 5px 0;
                overflow: hidden;
                max-width: 130px;
            }

            select {
                width: 90%;
            }
        }

    }

    .importer-footer {
        @include display-flex;
        @include justify-content(space-between);
        @include flex-wrap(wrap);
    }
}



