@import "variables";
@import "mixins";
@import "buttons";

.app {
  @include flex(1);
}

.text-center {
  text-align: center;
}

.padding {
  padding: 16px;
}

.loading {
  height: 200px;
  max-height: 100vh;
  @include display-flex;
  @include align-items(center);
  @include justify-content(center);
  img {
    height: 80px;
  }
}

#coz-bar {
    position: fixed;
    left: 0;
    right: 0;
    height: $cozyBarHeight;
    background-color: #fff;
    font-size: 15px; 
}



