@import "variables";

.button {
  border: solid 1px #9b9b9b;
  @include appearance(none);
  font-family: $fontFamily;
  font-size: 14px;
  letter-spacing: 1px;
  color: #dddddd;
  padding: 12px 15px;
  font-weight: 300;
  cursor: pointer;

  &.button-positive {
    background-color: $positive;
    color: #dddddd;
  }

  &.button-light {
    background-color: $light;
    color: #444;
  }

  &.button-assertive {
    background-color: $assertive;
    color: #FFF;
    border: 1px solid darken($assertive, 5%)!important;
    &:hover {
      background-color: lighten($assertive, 5%)!important;
    }
  }

  &.button-stable {
    background-color: $stable;
    color: #444;
  }

  &.button-default {
    background-color: $default;
    color: #dddddd;
    border:1px solid $default; 
  }

  &.button-clear {
    background-color: transparent;
    border: none;
  }

  > i:first-child {
    margin-right: 8px;
    vertical-align: middle;
  }

  &.button-icon {
    @include display-flex;
    @include align-items(center);
    @include justify-content(center);
    i:first-child {
      line-height: 0px;
      margin: 0px;
      padding: 0px;
      font-size: 25px;
      height: 18px;
      @include display-flex;
      @include align-items(center);
      @include justify-content(center);
      overflow: visible;
    }
  }

}


