@import "../variables";

.file {


    .img-viewer-box {
      display: flex;
      justify-content: center;
      align-items: center;
    }

    .img-viewer-box img{
      margin: 25px 0;
      max-width: 100%;
    }

    .pdf-viewer-box {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .pdf-viewer {
        display: inline;
        box-shadow: 0 0 15px grey;
        margin: 25px 0;
    }

    .pdf-page {
        display: flex;
        flex-direction: column;
        align-items: center;
        order: 2;
    }

    .pdf-viewer-box {
        .direction {
            font-size: 30px;
            padding: 25px 15px;
            cursor: pointer;

            transition: transform 0.5s;
        }

        .previous {
            order: 1;
        }

        .previous:hover {
            transform: translateX(-7px) scale(1.05);
        }

        .next {
            order: 3;
        }

        .next:hover {
            transform: translateX(7px) scale(1.05);
        }
    }

    .rename-box, .change-type-box {
        select {
            height: 40px;
            line-height: 40px;
            vertical-align: bottom;
            display: inline-block;
        }

        button.button {
            margin-left: 10px;
            padding: 0 13px;
            height: 40px;

            i {
                margin: 0;
                font-size: 33px;
            }
        }
    }

  .loading {
    height: 200px;
    max-height: 100vh;
    @include display-flex;
    @include align-items(center);
    @include justify-content(center);
    img {
      height: 80px;
    }
  }
  .button-import {
    input {
      opacity: 0;
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0px;
      right: 0px;
      bottom: 0px;
      top: 0px;
      z-index: -1;
      pointer-events: none;

    }
  }

}



