@import "../../variables";

.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100vw;
  height: 100vh;
  z-index: 999;
  display: none;
  @include align-items(flex-start);
  @include justify-content(center);
  background-color: rgba(0, 0, 0, 0.4);
  @include transition(all 0.2s linear);
  overflow: auto;

  &.modal-show {
      @include display-flex;
      opacity: 0;
  }

  &.modal-visible {
    opacity: 1;
  }

  .modal-loading {
    width: 50px;
    height: 100%;
    padding: 10px 0;

    img {
        width: 100%;
    }
  }

  .modal-container {
    margin-top: 100px;
    margin-bottom: 16px;
    width: 50vw;
    background: #FFF;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.2);
    padding: 16px;
    box-sizing: border-box;

  }

  @media screen and (max-width: 767px) {
    .modal-container {
      width: 100vw;
      min-height: 100vh;
      margin: 0px;
      box-shadow: none;
    }
  }
}



