@import "../../variables";

.label-icon {
  @include flex(1);
  @include display-flex;
  @include flex-direction(row);
  @include align-items(center);

  i {
    font-size: 30px;
    display: block;
    width: 30px;
    text-align: center;

    &.left {
        margin-right: 10px;
    }

    &.right {
        margin-left: 10px;
    }
  }
}



