@import "../variables";

.faq {

	.section-header{
		border-bottom: 0 none;
	}
	.panel {
		border-radius: 0;
	}
	.panel-title {
		min-height: 47px;
		font-size: 24px;
		margin-top: 17px;

	}

	.panel-title a {
		cursor: pointer;
		font-style: normal!important;
	}
	.panel-title a:hover {
		text-decoration: none!important;
	}
	.panel-title a[aria-expanded="false"]::before {
		content: "\f123";
	   	font-family: ionicons;
    	margin-right: 5px;
	}

	.panel-title a[aria-expanded="true"]::before {
		content: "\f126";
	   	font-family: ionicons;
    	margin-right: 5px;
	}

	li {
		list-style-type: none;
	}

	li h6::before {
		content: "\f125";
	   	font-family: ionicons;
    	margin-right: 5px;
	}
}



