@import '../variables';

.main-content {
  padding-top: $cozyBarHeight;
  padding-left: $navigationWidth;
  min-height: calc(100vh - #{$footerHeight} - #{$cozyBarHeight});
  @include display-flex;
  box-sizing: content-box;

  .main-content-container {
    padding: 16px 32px;
    @include flex;
    @include display-flex;
    width: 100%;
  }

  @media screen and (max-width: 767px) {
    padding-left: 0px;

    .main-content-container {
      padding: 16px;
    }
  }

}



