@import '../variables';

.navigation {
    width: $navigationWidth;
    background-color: #485d76;
    box-shadow: inset -1px 0 0 0 #202a35;
    position: fixed;
    top: $cozyBarHeight;
    left: 0;
    bottom: 0;
    @include display-flex;
    @include flex-direction(column);
    @media screen and (max-width: 767px) {
        display: none;
    }

    overflow: auto;

    > nav {
        &:first-child {
            @include flex(1);
        }

        ul {
            list-style: none;
            padding: 15px;

            li {
                a {
                    font-family: $fontFamily;
                    font-size: 16px;
                    font-weight: 300;
                    letter-spacing: 1px;
                    color: #dddddd;
                    text-decoration: none;
                    display: block;
                    padding: 14px 12px;

                    i {
                        margin-right: 14px;
                        color: #e4e4e4;
                    }
                }

                &.active,
                &:hover {
                    a {
                        background-color: #334254;
                        box-shadow: inset 0 1px 0 0 rgba(0, 0, 0, 0.5);

                        i {
                            color: $positive;
                        }
                    }
                }
            }
        }
    }

    .gauge-container {
        min-height: 200px;
        border: solid 1px #dddddd;
        margin: 0 auto;
        padding: 15px;
        overflow: hidden;

        .title {
            height: 40px;
            font-family: 'Roboto', sans-serif;
            font-size: 14px;
            font-weight: 300;
            line-height: 1.43;
            letter-spacing: 1px;
            color: #dddddd;
            margin-bottom: 9px;
        }

        .gauge {
            width: 160px;
            height: 14px;
            background-color: #334254;
            box-shadow: inset 0 1px 0 0 rgba(0, 0, 0, 0.5);
            margin: 0 auto 15px;
        }

        .progress {
            height: 14px;
            background-color: #c8c951;
            box-shadow: inset 0 1px 0 0 rgba(0, 0, 0, 0.5);
        }

        ul {
            list-style: none;
            padding: 0;
            margin: 0;

            li {
                font-family: 'Roboto', sans-serif;
                font-size: 12px;
                font-weight: 300;
                letter-spacing: 0.9px;
                color: #dddddd;
                margin-bottom: 10px;


                &:last-child {
                    margin-bottom: 0;
                }

                a {
                    color: #fff;
                    cursor: pointer;
                }
            }
        }
    }

    
}



