@font-face {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 100;
    src: url("./font/Roboto-Thin.ttf") format("truetype");
}

@font-face {
    font-family: 'Roboto';
    font-style: italic;
    font-weight: 100;
    src: url("./font/Roboto-ThinItalic.ttf") format("truetype");
}

@font-face {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 300;
    src: url("./font/Roboto-Light.ttf") format("truetype");
}

@font-face {
    font-family: 'Roboto';
    font-style: italic;
    font-weight: 300;
    src: url("./font/Roboto-LightItalic.ttf") format("truetype");
}

@font-face {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    src: url("./font/Roboto-Regular.ttf") format("truetype");
}

@font-face {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    src: url("./font/Roboto-Italic.ttf") format("truetype");
}

@font-face {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 700;
    src: url("./font/Roboto-Bold.ttf") format("truetype");
}

@font-face {
    font-family: 'Roboto';
    font-style: italic;
    font-weight: 700;
    src: url("./font/Roboto-BoldItalic.ttf") format("truetype");
}


body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}



