@import "../variables";

.data-detail {
  margin: 16px 0px;
  @include appearance(none);
  border: none;
  padding: 0px;

  table {
    border-spacing: 0;
    border-collapse: collapse;
    width: 100%;
    padding: 0px;
    border: none;
    table-layout: fixed;

    .data-detail-group {
      display: table-row-group;

      .data-detail-group-title {
        
        font-size: 30px;
        color: #32363F;
        letter-spacing: 1.25px;
        line-height: 40px;
      }

      td, th {
        border: none;
        padding: 16px;
        text-align: left;
        h2 {
          margin: 0px;
          padding: 0px;
          
          font-size: 30px;
          color: #32363F;
          letter-spacing: 1.25px;
          line-height: 40px;
        }
      }
    }

    .data-detail-item {
      td, th {
        border: 1px solid #AAA;
        padding: 16px;
        text-align: left;
      }

      th {
        font-size: 14px;
        color: #32363F;
        letter-spacing: 1.17px;
        background-color: #F6F6F6;
        font-weight: lighter;
        width: 320px;
      }

      td {
        /* 24/06/2017: */
        font-size: 14px;
        color: #32363F;
        letter-spacing: 1.17px;
        font-weight: 300;
        input, select {
          @include appearance(none);
          background: #FFFFFF;
          border: 1px solid #9B9B9B;
          border-radius: 2px;
          padding: 10px 16px;
          &[type="text"], &[type="number"] {
            display: block;
            width: calc(100% - 32px);
          }
        }
      }
    }
  }
}


